<template>
  <div id="app">
    <transition :name="transitionName" :mode="$router.back ? 'out-in' : 'in-out'">
      <router-view class="view"></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'app',
  created() {
    const counterMap = JSON.parse(localStorage.getItem('goods')) || {};
    this.$store.commit('setCounterMap', counterMap);
  },
  data() {
    return {
      transitionName: 'slide-left',
    };
  },
  watch: {
    $route(to, from) {
      if (to.name === 'classify' && from.name === 'search') {
        // 从search调到classify
        this.$router.back = true;
      }
      if (this.$router.back) {
        this.transitionName = 'slide-right'; // 右侧进入
      } else {
        this.transitionName = 'slide-left'; // 左侧进入
      }
      this.$router.back = false; // 方便下次使用
    },
  },
};
</script>

<style>
.view {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  transition: all 0.3s linear;
}
.slide-left-enter {
  transform: translate(100%, 0);
}

.slide-right-leave-to {
  transform: translate(100%, 0);
}

/* .slide-left-leave-active {
  transform: translate(-100%, 0);
} */
</style>
